import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import './components/image-gallery/image-gallery.css';
import './components/login-signin-screen/login-signin.css';
import Tweets from './noRTs';
import { InitAuthStateListener, logOut } from './auth';
import LoginSignInScreen from './components/login-signin-screen/login-signin.js';
import ImageGallery from './components/image-gallery/image-gallery.js';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import io from 'socket.io-client';
import { socket } from './socket.js';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      galleryIsVisible: false,
      tweetSelectedID: null,
      tweetSelectedIndex: null,
      tweetSelectedMedia: null,
      videoVolume: 0.5,
      loggedIn: null
    };

    this.componentWillMount = this.componentWillMount.bind(this);
  }

  setVolume(vol) {
    this.setState({
      videoVolume: vol
    });
  }

  getVolume = () => this.state.videoVolume;

  renderGallery(mediaArray, index) {
    document.querySelector('body').style.overflow = 'hidden';
    const mediaUrls = mediaArray.map(mediaItem => {
      return mediaItem.media_url;
    });

    this.setState({
      tweetSelectedIndex: index,
      tweetSelectedMedia: mediaUrls,
      galleryIsVisible: true
    });
  }

  closeGallery() {
    document.querySelector('body').style.overflow = 'auto';
    this.setState({
      galleryIsVisible: false
    });
  }

  async getInitialTweetsFromServer() {
    let tweetArray = [];

    const response = await fetch('http://localhost:3001/api/first_tweets');
    const tweets = await response.json();
    console.log(tweets);

    tweets.forEach(newTweet => {
      let media;
      if (newTweet.extended_entities !== undefined) {
        media = newTweet.extended_entities.media;
      } else {
        media = newTweet.media;
      }

      const tweetJSX = (
        <Tweet
          key={newTweet.id_str}
          text={this.decodeHtml(newTweet.text)}
          media={media}
          avatarUrl={newTweet.user.profile_image_url}
          name={newTweet.user.name}
          handle={newTweet.user.screen_name}
          tweetId={newTweet.id_str}
          retweetCount={newTweet.retweet_count}
          favoriteCount={newTweet.favorite_count}
          time={newTweet.created_at}
          openGallery={this.renderGallery.bind(this)}
          setVolume={this.setVolume.bind(this)}
          getVolume={this.getVolume.bind(this)}
        />
      );
      tweetArray = [...tweetArray, tweetJSX];
    });
    this.setState({
      tweets: tweetArray
    });
  }

  componentWillMount() {
    // this.setInitialTweets();
    InitAuthStateListener(this);
    // this.setTimer();
  }

  decodeHtml(html) {
    const txt = document.createElement('textarea');
    txt.innerHTML = html;
    return txt.value;
  }

  setTimer() {
    let prevIndexes = undefined;
    if (this.state.IndexesOfTweetsLoaded !== undefined) {
      prevIndexes = [...this.state.IndexesOfTweetsLoaded];
      if (prevIndexes.length === Tweets.length) {
        return;
      }
    }

    console.log(Tweets.length);
    setTimeout(() => {
      this.getNewTweet();
      this.setTimer();
    }, 7000);
  }

  getNewTweetIndex() {
    let rNumber = Math.round(Math.random() * 2385);
    // console.log(rNumber);
    return rNumber;
  }

  setInitialTweets() {
    let tweetArray = [];
    let numbers = [];

    for (let i = 0; i < 50; i++) {
      let rNumber = this.getNewTweetIndex();
      while (numbers.includes(rNumber)) {
        rNumber = this.getNewTweetIndex();
      }

      numbers.push(rNumber);

      let newTweet = Tweets[rNumber];

      let media;
      if (newTweet.extended_entities !== undefined) {
        media = newTweet.extended_entities.media;
      } else {
        media = newTweet.media;
      }

      const tweetJSX = (
        <Tweet
          key={rNumber}
          text={this.decodeHtml(newTweet.text)}
          media={media}
          avatarUrl={newTweet.user.profile_image_url}
          name={newTweet.user.name}
          handle={newTweet.user.screen_name}
          tweetId={newTweet.id_str}
          retweetCount={newTweet.retweet_count}
          favoriteCount={newTweet.favorite_count}
          time={newTweet.created_at}
          openGallery={this.renderGallery.bind(this)}
          setVolume={this.setVolume.bind(this)}
          getVolume={this.getVolume.bind(this)}
        />
      );
      tweetArray = [...tweetArray, tweetJSX];
    }

    console.log(tweetArray);

    this.setState({
      tweets: tweetArray,
      IndexesOfTweetsLoaded: numbers
    });
  }

  addTweetFromStream(newTweet) {
    const { tweets, IndexesOfTweetsLoaded } = this.state;
    let media;
    if (newTweet.extended_entities !== undefined) {
      media = newTweet.extended_entities.media;
    } else {
      media = newTweet.media;
    }

    const tweetJSX = (
      <Tweet
        key={newTweet.id_str}
        avatarUrl={newTweet.user.profile_image_url}
        name={newTweet.user.name}
        handle={newTweet.user.screen_name}
        text={this.decodeHtml(newTweet.text)}
        media={media}
        tweetId={newTweet.id_str}
        retweetCount={newTweet.retweet_count}
        favoriteCount={newTweet.favorite_count}
        time={newTweet.created_at}
      />
    );
    console.log(this.state.inde);

    this.setState({
      tweets: [tweetJSX, ...tweets]
    });
  }

  getNewTweet() {
    let rNumber = this.getNewTweetIndex();
    console.log(this.state.IndexesOfTweetsLoaded);
    while (
      this.state.IndexesOfTweetsLoaded.includes(rNumber) &&
      this.state.IndexesOfTweetsLoaded.length < Tweets.length
    ) {
      rNumber = this.getNewTweetIndex();
    }
    const { tweets, IndexesOfTweetsLoaded } = this.state;
    const newTweet = Tweets[rNumber];

    let media;
    if (newTweet.extended_entities !== undefined) {
      media = newTweet.extended_entities.media;
    } else {
      media = newTweet.media;
    }

    const tweetJSX = (
      <Tweet
        key={rNumber}
        avatarUrl={newTweet.user.profile_image_url}
        name={newTweet.user.name}
        handle={newTweet.user.screen_name}
        text={this.decodeHtml(newTweet.text)}
        media={media}
        tweetId={newTweet.id_str}
        retweetCount={newTweet.retweet_count}
        favoriteCount={newTweet.favorite_count}
        time={newTweet.created_at}
      />
    );
    console.log(document.querySelector('body').getBoundingClientRect().top);
    if (document.querySelector('body').getBoundingClientRect().top === 54) {
      this.setState({
        IndexesOfTweetsLoaded: [...IndexesOfTweetsLoaded, rNumber],
        tweets: [tweetJSX, ...tweets]
      });
    }
  }

  render() {
    return (
      <div>
        {this.state.loggedIn == null && (
          <LoginSignInScreen loggedIn={this.state.loggedIn} />
        )}
        {this.state.loggedIn !== null && (
          <div>
            {this.state.galleryIsVisible && (
              <ImageGallery
                closeGallery={this.closeGallery.bind(this)}
                imageClickIndex={this.state.tweetSelectedIndex}
                media={this.state.tweetSelectedMedia}
              />
            )}
            <NavBar />
            <TweetList
              addTweet={this.addTweetFromStream.bind(this)}
              getInitialTweets={this.getInitialTweetsFromServer.bind(this)}
              tweets={this.state.tweets}
            />
          </div>
        )}
      </div>
    );
  }
}

class TweetList extends React.Component {
  componentWillMount() {
    this.props.getInitialTweets();
    socket.on('tweet', tweet => {
      console.log(tweet);
      this.props.addTweet(tweet);
    });
  }

  render() {
    return (
      <div type="none" id="tweet-stream">
        <ReactCSSTransitionGroup
          transitionName="example"
          transitionEnterTimeout={500}
          transitionLeaveTimeout={300}
        >
          {this.props.tweets}
        </ReactCSSTransitionGroup>
      </div>
    );
  }
}

class Tweet extends React.Component {
  getTimeString() {
    let timeString;
    const currentYear = new Date().getFullYear().toString();
    const twitterTime = this.props.time.split(' ');

    if (twitterTime[2].charAt(0) === '0') {
      twitterTime[2] = twitterTime[2].charAt(1);
    }
    if (twitterTime[5] === currentYear) {
      timeString = twitterTime[1] + ' ' + twitterTime[2];
    } else {
      timeString = twitterTime[2] + ' ' + twitterTime[1] + ' ' + twitterTime[5];
    }
    return timeString;
  }

  getInteractionCountString(number) {
    const numString = number.toString();
    if (number > 999999) {
      number = `${numString[0]}.${numString[1]}M`;
    } else if (number > 99999) {
      number = `${numString[0]}${numString[1]}${numString[2]}K`;
    } else if (number > 9999) {
      number = `${numString[0]}${numString[1]}K`;
    } else if (number > 999) {
      number = `${numString[0]}.${numString[1]}K`;
    }
    return number;
  }

  filterText(text) {
    const separatedBySpaces = text.split(' ');
    let filteredArray = [];
    separatedBySpaces.forEach(string => {
      if (!string.includes('https://t.co/')) {
        filteredArray.push(string);
      }
    });
    const string = filteredArray.join(' ');
    return string;
  }

  handleImageClick(index) {
    this.props.openGallery(this.props.media, index);
  }

  setVolume() {
    const node = ReactDOM.findDOMNode(this);
    console.log(node.childNodes[1].childNodes);
    node.childNodes[1].childNodes.forEach(node => {
      if (node.classList.contains('tweet-video-container')) {
        node.childNodes[0].volume = this.props.getVolume();
      }
    });
  }

  getMediaDiv() {
    if (this.props.media === undefined) {
      return;
    }
    console.log(this.props.media.type);

    if (this.props.media[0].type === 'video') {
      return (
        <div className="tweet-video-container">
          <video
            onLoadStart={() => {
              this.setVolume(this);
            }}
            controls
            loop
            style={{
              borderRadius: '12px',
              marginBottom: '5px',
              border: 'solid 1px rgb(56, 68, 77)',
              maxHeight: '506px',
              maxWidth: '506px',
              backgroundColor: 'black',
              outline: 'none'
            }}
          >
            <source src="https://video.twimg.com/ext_tw_video/1199058926144278528/pu/vid/480x480/hrV39o4qUOyn8BcT.mp4?tag=10"></source>
          </video>
        </div>
      );
    }

    if (this.props.media.length === 1) {
      return (
        <img
          className="attached-image"
          src={this.props.media[0].media_url}
          alt="media-one"
          onClick={() => this.handleImageClick(0)}
        ></img>
      );
    } else if (this.props.media.length === 2) {
      return (
        <div className="tweet-image-container">
          <img
            className="media-length-two-image-one"
            src={this.props.media[0].media_url}
            onClick={() => this.handleImageClick(0)}
            alt="media-one"
          ></img>
          <div className="media-length-two-divider"></div>
          <img
            className="media-length-two-image-two"
            src={this.props.media[1].media_url}
            onClick={() => this.handleImageClick(1)}
            alt="media-two"
          ></img>
        </div>
      );
    } else if (this.props.media.length === 3) {
      return (
        <div className="media-length-three-container">
          <img
            className="media-length-three-image-one"
            src={this.props.media[0].media_url}
            onClick={() => this.handleImageClick(0)}
            alt="media-one"
          />
          <div className="media-length-three-vert-divider"></div>
          <div className="media-length-three-second-container">
            <img
              className="media-length-three-image-two"
              src={this.props.media[1].media_url}
              onClick={() => this.handleImageClick(1)}
              alt="media-two"
            />
            <div className="media-length-three-hor-divider"></div>
            <img
              className="media-length-three-image-three"
              src={this.props.media[2].media_url}
              onClick={() => this.handleImageClick(2)}
              alt="media-three"
            />
          </div>
        </div>
      );
    } else if (this.props.media.length === 4) {
      return (
        <div className="media-length-four-container">
          <img
            className="media-length-four-image-one"
            src={this.props.media[0].media_url}
            onClick={() => this.handleImageClick(0)}
            alt="media-one"
          />
          <div className="media-length-three-vert-divider"></div>
          <div className="media-length-four-second-container">
            <img
              className="media-length-four-image-two"
              src={this.props.media[1].media_url}
              onClick={() => this.handleImageClick(1)}
              alt="media-two"
            />
            <div className="media-length-four-hor-divider"></div>
            <img
              className="media-length-four-image-three"
              src={this.props.media[2].media_url}
              onClick={() => this.handleImageClick(2)}
              alt="media-three"
            />
            <div className="media-length-four-hor-divider"></div>
            <img
              className="media-length-four-image-four"
              src={this.props.media[3].media_url}
              onClick={() => this.handleImageClick(3)}
              alt="media-four"
            />
          </div>
        </div>
      );
    }
  }

  getTweetHref() {
    const { tweetId, handle } = this.props;
    return `https://twitter.com/${handle}/status/${tweetId}`;
  }

  render() {
    return (
      <div className="tweet">
        <a
          className="avatar-profile-anchor"
          href={`https://twitter.com/${this.props.handle}`}
        >
          <img src={this.props.avatarUrl} alt="avatar" className="avatar" />
        </a>
        <div className="tweet-content">
          <div className="tweet-top-bar">
            <a
              className="tweet-name"
              href={`https://twitter.com/${this.props.handle}`}
            >
              {this.props.name}
            </a>
            <p className="tweet-at">@</p>
            <p className="tweet-handle">{this.props.handle}</p>
            <div className="tweet-date-dot">.</div>
            <a className="tweet-date" href={this.getTweetHref()}>
              {this.getTimeString()}
            </a>
          </div>
          <p className="tweet-text">{this.filterText(this.props.text)}</p>
          {this.getMediaDiv()}
          <div className="tweet-action-bar">
            <span class="icon-retweet"></span>
            <div className="retweet-counter">
              {this.getInteractionCountString(this.props.retweetCount)}
            </div>
            <span class="icon-favorite"></span>
            <div className="favorite-counter">
              {this.getInteractionCountString(this.props.favoriteCount)}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

class NavBar extends React.Component {
  render() {
    return (
      <nav id="nav-bar">
        <div id="twitter-icon-wrapper">
          <img
            id="navbar-twitter-icon"
            src="./img/twittericon.png"
            alt="twitter-icon"
          />
        </div>
        <button id="logout-button" onClick={logOut}>
          Log out
        </button>
      </nav>
    );
  }
}

ReactDOM.render(<App />, document.getElementById('root'));
